import React from "react"
import ZapierForm from "react-zapier-form"

export default function Zapier() {
  return (
    <ZapierForm action="https://hooks.zapier.com/hooks/catch/2886315/oaymgh6/">
      {({ error, loading, success }) => {
        return (
          <div className="form">
            {!success && !loading && (
              <div>
                <label htmlFor="text">
                  Ваше имя <span>*</span>
                  <input
                    type="text"
                    name="Name"
                    placeholder="Имя"
                    required
                    autocomplete="on"
                  />
                </label>
                <label htmlFor="tel">
                  Номер телефона <span>*</span>
                  <input
                    type="tel"
                    name="Tel"
                    placeholder="8 (999) 000-99-00"
                    required
                    autocomplete="on"
                  />
                </label>
                <label htmlFor="email">
                  Email
                  <input
                    type="email"
                    name="Email"
                    placeholder="Email"
                    autocomplete="on"
                  />
                </label>
                <button>Отправить</button>
                <center>
                  <span className="privacy">
                    <span>*</span> Нажимая отправить вы соглашатесь с{" "}
                    <a
                      href="https://algoritmika.org/privacy"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Политикой конфиденциальности
                    </a>
                  </span>
                </center>
              </div>
            )}
            {/* {loading && <div>Готовим для вас индивидуальную презентацию</div>} */}
            {error && <div>Что-то пошло не так. Попробуйте еще.</div>}
            {success && (
              <p>
                Спасибо! Посетите наш сайт{" "}
                <a href="https://algoritmika.org/?lead_form">
                  Политикой конфиденциальности
                </a>{" "}
              </p>
            )}
          </div>
        )
      }}
    </ZapierForm>
  )
}
